    },
    changeCity() {
      this.district = null;
      this.street = null;
    },
    changeDistrict() {
      this.street = null;
    },

    countDownChanged(dismissCountDown) {
      this.showDismissibleAlert = false;
      this.landCompensationFee = parseFloat((this.area * this.street.landCompensationFee).toFixed(2));
      this.settlementAssistanceFee = parseFloat((this.area * this.street.settlementAssistanceFee).toFixed(2));
      this.finalFee = this.landCompensationFee + this.settlementAssistanceFee;
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
