        console.log();
      } else if (this.value == "") {
        this.message = "请选择征地范围！";
        this.showDismissibleAlert = true;
      } else if (this.selected == "") {
        this.message = "请选择土地类型！";
        this.showDismissibleAlert = true;
      } else if (this.area == "") {
        this.message = "要记得填面积！";
        this.showDismissibleAlert = true;
      } else {
        this.message = "啥都没填？算不出来!";
        this.showDismissibleAlert = true;
      }
    },
